@font-face {
  font-family: Chopin;
  src: url(./fonts/chopin-script.regular.ttf);
}
@font-face {
  font-family: Cassandra;
  src: url(./fonts/cassandra-personal-use.regular.ttf);
}
@font-face {
  font-family: Chalk;
  src: url(./fonts/KGBrokenVesselsSketch.ttf);
}
@font-face {
  font-family: Kavo;
  src: url(./fonts/kavo-serif.regular.otf);
}

@import url('https://fonts.googleapis.com/css2?family=Red+Rose:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap');

body {
  margin: 0 !important;
  padding: 0 !important;
  font-family: Montserrat, sans-serif !important;
  font-weight: 500 !important;
}
body,
html {
  height: 100%;
  position: relative;
}
#root {
  min-height: inherit !important;
}
.App {
  min-height: 100% !important;
}
.section-heading {
  font-family: Chopin, cursive;
  font-size: 8rem;
  text-align: center;
  padding: 1% 0;
  font-weight: 600;
  color: #581d07;
}
:focus {
  outline: 0 !important;
  outline-width: 0 !important;
  box-shadow: none !important;
}
.main-page {
  min-height: inherit !important;
  height: inherit;
}
.main-section {
  background-image: url('./images/main-section.jpeg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  height: 100vh;
}
.main-title-container {
  padding-top: 15%;
}
.main-section-couple-pic {
  /* border-radius: 45%; */
  width: 90%;
  /* height: 0%; */
  /* margin-top: 8%; */
  animation: MoveLeftRight 2s linear infinite;
}
@keyframes MoveLeftRight {
  0%,
  100% {
    transform: translateX(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
.layer {
  background-color: rgba(219, 197, 32, 0.3);
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 2;
  padding-top: 10vh;
}
.wedding-of-img {
  display: block;
  margin: auto;
  width: 55%;
  margin-bottom: 2%;
}
.main-title {
  text-align: center;
  font-family: Kavo;
  font-size: 5.5vw;
  text-transform: uppercase;
  color: #47080a;
  text-shadow: 1px 1px 15px rgba(0, 0, 0, 0.56);
  letter-spacing: 2px;
  font-weight: 900;
}
.main-section-rsvp {
  margin-right: 0;
  text-align: right;
  color: #47080a !important;
  font-weight: 500;
  letter-spacing: 1px;
  font-size: 1.3rem;
  margin-top: 8%;
  text-shadow: 1px 1px 15px rgba(0, 0, 0, 0.56);
}
.main-section-rsvp:focus,
.main-section-rsvp:hover {
  text-decoration: none;
}
.countdown-container {
  background-color: #2e1031;
  color: #f5ba7a;
  z-index: 10;
  width: 60%;
  text-align: center;
  margin: auto;
  font-family: 'Red Rose';
  font-weight: 700;
  padding: 8px;
  position: absolute;
  bottom: -5vh;
  left: 20%;
  border-radius: 3px;
  clear: both;
}
.count {
  font-size: 3vmax;
}
.count-unit {
  font-size: 1.2vmax;
}
.invitation-section {
  background-color: #86211e;
  background-image: url('https://www.transparenttextures.com/patterns/45-degree-fabric-dark.png');
}
.invitation-container .container-fluid {
  height: inherit !important;
}
.invitation-container .col-md-5,
.invitation-container .col-md-7 {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.invitation-pic {
  min-height: 100%;
  max-width: 100%;
}
.content-container {
  padding: 10% 2%;
  text-align: center;
  background-color: #86211e;
  background-image: url('https://www.transparenttextures.com/patterns/45-degree-fabric-dark.png');
  min-height: 100%;
}
.invitation-names {
  font-family: Chopin;
  color: #f8ba5d;
  padding-top: 2.5%;
  padding-bottom: 2.4%;
  font-size: 5rem;
}
.assisting-text {
  text-transform: uppercase;
  color: #f1dba9;
  font-family: Montserrat, serif;
  font-weight: 600;
  font-size: 1.5rem;
  letter-spacing: 1px;
  padding-top: 2%;
  padding-bottom: 2%;
}
.invitation-venue-details {
  text-transform: capitalize;
}
.invitation-date {
  font-size: 3.1rem;
  font-weight: 900;
  letter-spacing: 2px;
}
.lamp1 {
  width: 80%;
  height: 450px !important;
  float: right;
}
.lamp2 {
  width: 80%;
  height: 450px !important;
  float: left;
}
.lamp-col {
  padding: 0 !important;
}
.introduction-section {
  background-image: url('./images/15130b7c6419da0f4f2bc281a8dc4b9e.jpg');
  background-attachment: fixed;
  background-position: top center;
  padding-bottom: 3%;
}
.intro-heading {
  color: ivory;
}
.intro-cards {
  text-align: center;
}
.card-profile {
  display: inline-block;
  position: relative;
  width: 100%;
  margin: auto;
  margin-bottom: 30px;
  border-radius: 6px;
  background-color: rgba(26, 2, 2, 0.7);
  background-image: url('https://www.transparenttextures.com/patterns/cream-paper.png');
  background-attachment: fixed;
  border-color: ivory !important;
  border-width: 4px !important;
  box-shadow: 0 2px 2px 0 rgb(0 0 0 / 14%), 0 3px 1px -2px rgb(0 0 0 / 20%),
    0 1px 5px 0 rgb(0 0 0 / 12%);
  margin-top: 30px;
  text-align: center;
}
.card-profile .card-avatar {
  max-width: 250px;
  border-radius: 100%;
  margin: -50px auto 0;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 2px 0,
    rgba(60, 64, 67, 0.15) 0 2px 6px 2px;
  border-width: 4px !important;
  border: 4px solid ivory;
  transition-duration: 0.15s;
}
.card-profile .card-avatar:hover {
  box-shadow: rgba(0, 0, 0, 0.56) 0 22px 70px 4px;
}
.card-profile img {
  width: 100%;
  height: auto;
}
.card-profile .table {
  padding: 15px 30px;
}
.table {
  margin-bottom: 0;
}
.card-caption {
  font-weight: 800;
  font-size: 2rem;
  font-family: Cassandra, cursive;
  color: ivory;
}
.card-caption,
.card-caption a {
  text-decoration: none;
}
.card-description {
  font-family: Montserrat;
  font-size: 1.1rem;
  font-weight: 500;
  color: #fff;
}
.parents-section {
  background: #fdfaee;
  background: linear-gradient(180deg, #fdfaee 0, #b55a37 100%);
  padding-bottom: 7%;
}
.parents-pic {
  border-radius: 100%;
  width: 70%;
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.24) 0 3px 8px;
}
.parents-block {
  border-radius: 6px;
  height: 90%;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  background-color: #fff;
  padding: 5%;
  box-shadow: rgba(0, 0, 0, 0.24) 0 3px 8px;
  text-align: justify;
}
.family-section {
  background-image: url('../src/images/family-bg1.jpg');
  background-size: cover;
  padding-bottom: 5%;
}
.family-section .carousel-indicators {
  height: 2px;
  position: absolute;
  bottom: 0 !important;
}
.family-section .section-heading {
  color: #7e1427;
}
.family-section .carousel-indicators button {
  border: 0 transparent solid;
  height: 3px;
  width: 27px;
  margin-right: 1.2%;
  background-color: #9120319d;
}
.family-section .carousel-indicators .active {
  background-color: #7e1427;
}
.bubble {
  height: auto;
  width: 100%;
  padding: 4% 5% 2%;
  min-height: 250px;
  background: #923f4e;
  background-image: url('https://www.transparenttextures.com/patterns/classy-fabric.png');
  display: block;
  color: #fff;
  margin: 20px auto;
  border-radius: 20px;
  font-size: 1rem;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.16) 0 10px 36px 0, rgba(0, 0, 0, 0.06) 0 0 0 1px;
}
.pointer {
  height: 40px;
  width: 40px;
  background: #923f4e;
  background-image: url('https://www.transparenttextures.com/patterns/classy-fabric.png');
  margin: 0 auto;
  transform: rotate(45deg);
  position: relative;
}
.parents-pointer {
  border-radius: 0 0 12px 0;
  margin-top: -40px;
  left: calc(0.5vw - 31%);
}
.groom-sibling-pointer {
  margin-left: -20px;
  top: calc(0.5vh - 60%);
  border-radius: 0 0 0 12px;
}
.bride-sibling-pointer {
  margin-right: -15px;
  top: calc(0.5vh - 50%);
  border-radius: 0 12px 0 0;
}
.bubble-img-container {
  position: relative;
  top: 2%;
}
.bubble-img-container img {
  border-radius: 100%;
  width: 95%;
  display: block;
  position: relative;
  border: 6px #fff solid;
  box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
}
.parents-msg,
.sibling-msg {
  color: #fff;
}
.sibling-msg .family-member-intro {
  text-align: center;
  font-weight: 700;
  color: #7e1427;
  margin-top: 15%;
}
.sibling-msg .family-member-name {
  font-family: Cassandra, cursive;
  font-size: 1.2rem;
}
.sibling-msg .family-member-relation {
  font-size: 1rem;
  text-transform: uppercase;
}
.sibling-msg .col-md-5,
.sibling-msg .col-md-7 {
  padding: 0 !important;
}
.parents-msg .family-member-intro {
  text-align: left;
  position: relative;
  top: 35%;
  font-weight: 700;
  font-family: Cassandra, cursive;
  color: #7e1427;
  font-size: 1.5rem;
}
.parents-msg .bubble {
  min-height: 292px !important;
}
.bubble-text {
  white-space: 'pre-wrap';
}
.read-more-btn {
  padding: 1%;
  color: #fff;
  text-decoration: underline;
  font-weight: 500;
  border: none !important;
  border-radius: 10px;
  background: 0 0;
}
.friends-section {
  background: #b55a37;
  background: linear-gradient(180deg, #b55a37 0, #d19413 100%);
  background-image: url('./images/15130b7c6419da0f4f2bc281a8dc4b9e.jpg');
  background-attachment: fixed;
  padding-bottom: 7%;
}
.friends-heading {
  color: #fff;
}
.friends-section .carousel-indicators {
  height: 1px;
  position: absolute;
  bottom: -15px !important;
}
.friends-section .carousel-indicators button {
  border: 1px transparent solid;
  width: 30px;
  margin-right: 1.5%;
  background-color: rgba(247, 142, 142, 0.7);
}
.friends-section .carousel-indicators .active {
  background-color: #5f1523;
}
.friends-card {
  margin-bottom: 10px;
  padding: 2%;
  padding-bottom: 4%;
  background-color: #f7e4e6;
  background-image: url('https://www.transparenttextures.com/patterns/corrugation.png');
  align-content: justify !important;
  border-radius: 20px !important;
  box-shadow: rgba(0, 0, 0, 0.16) 0 3px 6px, rgba(0, 0, 0, 0.23) 0 3px 6px;
  min-height: 450px;
  height: auto;
}
.friends-card .card-body {
  padding-bottom: 10px !important;
}
.friends-card-img {
  border-radius: 100%;
  width: 160px;
  height: 160px;
  border: 5px #fff solid;
  box-shadow: rgba(0, 0, 0, 0.16) 0 1px 4px;
  display: block;
  margin: auto;
}
.friends-intro-container {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.friend-name {
  font-weight: 600;
  font-size: 1.5rem;
  font-family: Cassandra, cursive;
  color: #86211e;
  letter-spacing: 1px;
}
.sub-heading {
  font-size: 2rem;
  font-weight: 700;
  margin-top: 2%;
  margin-bottom: 2%;
  font-family: Cassandra, cursive;
  color: #fff;
  letter-spacing: 1px;
}
.friends-msg {
  font-weight: 500;
  color: #000;
}
.bridesmaid-heading {
  text-align: right;
  margin-top: 7%;
}
.friends-section .read-more-btn {
  color: #000;
}
.venue-section {
  padding-bottom: 5%;
  background-image: url('../src/images/venue-bg1.jpg');
}
.venue-heading {
  color: #7e1427;
}
.venue-card {
  padding: 10%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 1%;
  margin-bottom: 20px;
  box-shadow: rgba(99, 99, 99, 0.2) 0 2px 8px 0;
  min-height: 100% !important;
  height: 100%;
  border-radius: 20px;
  transition: top ease 0.5s;
  position: relative;
  top: 0;
}
.venue-card:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0 54px 55px, rgba(0, 0, 0, 0.12) 0 -12px 30px,
    rgba(0, 0, 0, 0.12) 0 4px 6px, rgba(0, 0, 0, 0.17) 0 12px 13px,
    rgba(0, 0, 0, 0.09) 0 -3px 5px;
  top: -12px;
}
.venue-container {
  height: 100%;
  background-color: #fff !important;
  padding: 6%;
  border-radius: 5px;
  text-align: center;
}
.venue-card-icon {
  height: 100px;
}
.venue-card-function {
  font-weight: 700;
  font-size: 1.3rem;
  color: #6b4018;
  font-family: Cassandra, cursive;
  margin-top: 10px;
}
.venue-card-details-container {
  font-size: 1.1rem;
  color: #161515;
  margin-top: 10px;
  font-weight: 500;
}
.venue-name {
  font-weight: 600;
  color: #925923;
  font-size: 1.1rem;
}
.venue-card-contact {
  font-size: 1.15rem;
  font-weight: 700;
}
.venue-card-contact a {
  color: #925923 !important;
}
.venue-card-map-button {
  background-color: transparent;
  color: #925923;
  border: #925923 solid 2px;
  font-weight: 500;
  padding: 2% 5% !important;
}
.venue-card-map-button:hover {
  background-color: #925923;
  color: #fff;
}
.rsvp-section {
  padding-top: 2%;
  padding-bottom: 7%;
  background-image: url('../src/images/rsvp-bg1.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  bottom: 0;
}
.rsvp-heading {
  color: #7e1427;
  font-family: 'Red Rose', sans-serif;
  text-transform: uppercase;
  font-weight: 800;
  transition-duration: 0.1s;
  font-size: 7rem;
  text-shadow: 0 0 1px #7e1427, 0 0 1px #7e1427, 0 0 5px #7e1427,
    0 0 10px #f8f1f1, 0 0 0 #f8f1f1, 0 0 0 #f8f1f1, 0 0 0 #f8f1f1,
    0 0 50px #f8f1f1;
}
.form-container {
  width: 60%;
  background-color: #f0e8e8;
  background-image: url('https://www.transparenttextures.com/patterns/arches.png');
  border-radius: 4px;
  padding: 3% 6% 5%;
  margin: auto;
}
.form-heading {
  font-size: 2.2rem;
  font-weight: 600;
  margin-bottom: 3%;
  color: #912031;
  letter-spacing: 1px;
}
.form-body {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 5%;
  color: #912031;
}
.rsvp-form-btn {
  border-radius: 30px;
  padding: 2% 4%;
  background-color: #912031;
  color: #fff;
  border: none;
}
.rsvp-form-label {
  font-weight: 600 !important;
}
.rsvp-form-btn:focus,
.rsvp-form-btn:hover {
  background-color: #50111b !important;
}
.form-msg {
  font-weight: 600;
  font-size: 1.3rem;
  color: #912031;
  margin-top: 5%;
}
input[type='radio']:after {
  width: 17px;
  height: 17px;
  border-radius: 17px;
  top: -2px;
  left: -1px;
  position: relative;
  background-color: rgb(199, 179, 179);
  content: '';
  display: inline-block;
  visibility: visible;
  /* border: 2px solid white; */
}

input[type='radio']:checked:after {
  width: 17px;
  height: 17px;
  border-radius: 17px;
  top: -2px;
  left: -1px;
  position: relative;
  background-color: #7a1316;
  content: '';
  display: inline-block;
  visibility: visible;
  /* border: 2px solid white; */
}
.hashtag-section {
  background-image: url('./images/15130b7c6419da0f4f2bc281a8dc4b9e.jpg');
  background-attachment: fixed;
  padding: 2%;
  border: 10px solid #f1e5e8;
  box-shadow: rgba(50, 50, 93, 0.25) 0 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0 18px 36px -18px inset;
}
.hashtag-section .hashtag {
  font-size: 10vmin;
  color: #f1e5e8;
  text-align: center;
  font-family: Chalk, 'Red Rose', Montserrat, sans-serif;
}
.hashtag-section .hashtag-icons {
  text-align: center;
  font-size: 3vmax;
  color: #f1e5e8;
}
.hashtag-section a {
  color: #f1e5e8 !important;
}
.hashtag-section a:hover {
  color: #bbadb1 !important;
}
@media screen and (max-width: 450px) {
  .main-title {
    font-size: 4rem;
    top: 60%;
  }
  .section-heading {
    font-size: 4rem !important;
  }
  .countdown-container {
    bottom: -2vh;
    width: 90%;
    left: 5%;
  }
  .invitation-section {
    min-height: 100vh !important;
  }
  .invitation-pic {
    min-width: 100% !important;
    max-height: 50% !important;
  }
  .assisting-text {
    font-size: 1rem;
    padding-top: 5%;
  }
  .invitation-names {
    font-size: 3.5rem;
  }
  .invitation-names span {
    display: block;
  }
  .invitation-date {
    font-size: 1.8rem;
  }
  .invitation-venue {
    margin-top: 5%;
  }
  .form-container {
    width: 90%;
    margin: 6% auto;
  }
  .form-heading {
    font-size: 1.8rem;
  }
  .form-msg {
    font-size: 1rem;
  }
  .rsvp-form-btn {
    padding: 3% 5%;
  }
}
@media screen and (max-width: 500px) {
  .main-section {
    background-image: url('./images/Chaitanya-Molika-Caricature.jpg');
    background-position: center center !important;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .layer {
    background-color: transparent;
  }
}
@media screen and (min-width: 501px) and (max-width: 767px) {
  .main-section {
    height: fit-content;
  }
  .main-title-container {
    padding-top: 5%;
  }
  .wedding-of-img {
    width: 60%;
  }
  .main-title {
    font-size: 10vw;
  }
  .main-section-couple-pic {
    width: 60%;
    display: block;
    margin: auto;
    margin-bottom: 15%;
  }
}
@media screen and (min-width: 768px) and (max-width: 1200px) {
  .main-section {
    height: fit-content !important;
  }
  .main-section-couple-pic {
    margin-bottom: 35%;
  }
}
@media screen and (min-width: 450px) and (max-width: 650px) {
  .countdown-container {
    bottom: -3vh;
    width: 80%;
    left: 10%;
  }
  .invitation-date {
    font-size: 2.8rem;
  }
  .section-heading {
    font-size: 5rem;
  }
  .form-container {
    width: 80%;
  }
}
@media screen and (min-width: 650px) and (max-width: 1050px) {
  .invitation-date {
    font-size: 2.8rem;
  }
  .section-heading {
    font-size: 6.5rem;
  }
  .form-container {
    width: 70%;
  }
  .card-profile {
    width: 100%;
    min-height: 100% !important;
  }
}
@media (max-width: 767px) {
  .section-heading {
    padding-top: 7%;
  }
  .invitation-section {
    background-color: #fff !important;
    background-image: url('./images/sheer-window-curtains-white-gold-vintage-wallpaper.jpg.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .content-container {
    background-image: none !important;
    background-color: transparent !important;
    min-height: 100% !important;
  }
  .assisting-text {
    color: #570e1c;
    font-weight: 700;
  }
  .invitation-names {
    color: #570e1c;
    font-weight: 700;
  }
  .card-profile,
  .card-profile .card-avatar {
    border-color: #fff !important;
  }
  .card-profile .card-caption {
    font-size: 1.5rem;
  }
  .card-profile .card-description {
    font-size: 1rem;
  }
  .family-section {
    padding-bottom: 20%;
  }
  .parents-bubble .bubble {
    margin-bottom: 6px;
  }
  .parents-pointer {
    top: calc(0.5vh - 5px);
  }
  .sibling-msg .bubble {
    margin-bottom: 0;
  }
  .speech-bubble .col-5,
  .speech-bubble .col-7,
  .speech-bubble .col-sm-5,
  .speech-bubble .col-sm-7 {
    padding: 0 !important;
  }
  .speech-bubble .family-member-intro {
    text-align: left !important;
    margin-left: 5%;
  }
